import common from "../../assets/common/common.js";
import $ from "jquery";
export default {
  name: "app",
  data() {
    return {
      msg: {},
      info: "申请达人",
      is: true,
      isColro: false,
      android: "",
      iphone: "",
      member_id: this.$route.query.member_id
    };
  },
  created() {
    this.getId();
  },
  mounted() {},
  methods: {
    getId: function() {
      var _this = this;
      // 判断string字符串里面,是不是纯数字
      function isNumber(val) {
        var regPos = /^\d+(\.\d+)?$/; //非负浮点数
        var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //负浮点数
        if (regPos.test(val) || regNeg.test(val)) {
          return true;
        } else {
          return false;
        }
      }
      var url = "";
      // 判断传过来的id,是不是存数字,是纯数字,就执行第一个v1接口,不是纯数字,就是执行v3接口.
      if (isNumber(_this.member_id)) {
        console.log("是纯数字");
        url =
          common.baseUrl +
          "/v1/member/info?target_member_id=" +
          _this.member_id;
      } else {
        console.log("是字符串");
        url = common.baseUrl + "/v3/member/info?member_id=" + _this.member_id;
      }
      $.ajax({
        type: "get",
        url: url,
        beforeSend: function() {
          $("#loading").show();
        },
        complete: function() {
          $("#loading").hide();
        },
        success: function(res) {
          // console.log(res.data);
          // 状态码,判断状态吗,如果等于他,就返回消息给安卓
          if (res.code == 21005) {
            var inpObj = "TOKEN_ERROR";
            inpObj = inpObj + "";
            console.log(inpObj);
            if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
              window.webkit.messageHandlers["Native"].postMessage(inpObj);
            } else {
              // 安卓传输不了js json对象
              // console.log(inpObj)
              window.android.get(inpObj); //安卓方法
            }
          }

          if (res.data.master == "REVIEW") {
            _this.info = "审核中";
            _this.isColro = true;
          }
          if (res.data.master == "COMMON" || res.data.master == "OFFICIAL") {
            window.location.href = "/#/becomeExpert?member_id=" + _this.member_id;
          }
          // 把数据负值给data的定义的空对象
          _this.msg = res.data;
          // console.log(res.data);
          if (res.data.master == "NONE") {
            // console.log('none')
            return;
          }
        },
        error: function(err) {
          console.log(err);
          console.log("失败");
        }
      });
    },
    getExpert() {
      var _this = this;
      $.ajax({
        type: "post",
        url: common.baseUrl + "/v2/member_master",
        success: function(res) {
          // 跟换内容
          _this.info = "审核中";
          // 动态添加一个类,添加颜色;
          _this.isColro = true;
          if (res.data.master == "REVIEW") {
            _this.info = "审核中";
            _this.isColro = true;
          }
          // 存储数据
        },
        error: function(err) {
          console.log(err);
        }
      });
    },
    // 安卓——ios调用方法
    myFunction_inforMation() {
      var inpObj = $(".info_gl>p>input").attr("data-id");
      inpObj = inpObj + "";
      if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
        window.webkit.messageHandlers["Native"].postMessage(inpObj);
      } else {
        // 安卓传输不了js json对象
        console.log(inpObj);
        window.android.get(inpObj); //安卓方法
      }
    },
    myFunction_strategy() {
      var inpObj = $(".strategy_gl>p>input").attr("data-id");
      inpObj = inpObj + "";
      // window.android.get(inpObj);
      if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
        window.webkit.messageHandlers["Native"].postMessage(inpObj);
      } else {
        //安卓传输不了js json对象
        console.log(inpObj);
        window.android.get(inpObj); //安卓方法
      }
    },
    myFunction_lesson() {
      var inpObj = $(".lesson_gl>p>input").attr("data-id");
      inpObj = inpObj + "";
      // window.android.get(inpObj);
      if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
        window.webkit.messageHandlers["Native"].postMessage(inpObj);
      } else {
        //安卓传输不了js json对象
        console.log(inpObj);
        window.android.get(inpObj); //安卓方法
      }
    },
    myFunction_stroll() {
      var inpObj = $(".stroll_gl>p>input").attr("data-id");
      inpObj = inpObj + "";
      // window.android.get(inpObj);
      if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
        window.webkit.messageHandlers["Native"].postMessage(inpObj);
      } else {
        //安卓传输不了js json对象
        console.log(inpObj);
        window.android.get(inpObj); //安卓方法
      }
    }
  }
};
