import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import BecomeExpert from "./views/becomeExpert/becomeExpert.vue";
import Experience from "./views/experience/experience.vue";
import Expert from "./views/expert/expert.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/about",
      name: "about",
      component: About
    },
    {
      path: "/becomeExpert",
      name: "BecomeExpert",
      component: BecomeExpert
    },
    {
      path: "/experience",
      name: "Experience",
      component: Experience
    },
    {
      path: "/expert",
      name: "Expert",
      component: Expert
    }
  ]
});
