import common from "../../assets/common/common.js";
import $ from "jquery";
export default {
  name: "app",
  data() {
    return {
      msg: {},
      //   target_member_id: this.$route.query.target_member_id,
      //   member_id: this.$route.query.member_id
      member_id: this.$route.query.member_id,
      abc: "哈哈哈"
    };
  },
  created() {
    this.getuser();
  },
  methods: {
    getuser: function() {
      var _this = this;
      // 判断string字符串里面,是不是纯数字
      function isNumber(val) {
        var regPos = /^\d+(\.\d+)?$/; //非负浮点数
        var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //负浮点数
        if (regPos.test(val) || regNeg.test(val)) {
          return true;
        } else {
          return false;
        }
      }

      var url = "";
      // 判断传过来的id,是不是存数字,是纯数字,就执行第一个v1接口,不是纯数字,就是执行v3接口.
      if (isNumber(_this.member_id)) {
        console.log("是纯数字");
        url = `${common.baseUrl}/v1/member/info?target_member_id=${
          _this.member_id
        }`;
      } else {
        console.log("是字符串");
        // url = common.baseUrl + "/v3/member/info?member_id=" + _this.member_id;
        url = `${common.baseUrl}/v3/member/info?member_id=${_this.member_id}`;
      }
      $.ajax({
        type: "get",
        url: url,
        beforeSend: function() {
          $("#loading").show();
        },
        complete: function() {
          $("#loading").hide();
        },
        success: function(res) {
          // console.log(res.data);
          _this.msg = res.data;
        },
        error: function(err) {
          console.log(err);
        }
      });
    }
  }
};
